import dnIcon from '@/components/dn-icon/dn-icon.vue'
import tTable from '@/components/t-table/t-table.vue';
import tTh from '@/components/t-table/t-th.vue';
import tTr from '@/components/t-table/t-tr.vue';
import tTd from '@/components/t-table/t-td.vue';

const DETAIL_PAGE_PATH = '/pages/template/list2detail-detail/list2detail-detail';

import htmlParser from '@/common/html-parser'

function _handleShareChannels(provider) {
	let channels = [];
	for (let i = 0, len = provider.length; i < len; i++) {
		switch (provider[i]) {
			case 'weixin':
				channels.push({
					text: '分享到微信好友',
					id: 'weixin',
					sort: 0
				});
				channels.push({
					text: '分享到微信朋友圈',
					id: 'weixin',
					sort: 1
				});
				break;
			default:
				break;
		}
	}
	channels.sort(function(x, y) {
		return x.sort - y.sort;
	});
	return channels;
}

export default {
	components: {
		dnIcon,
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			title: '',
			banner: {},
			htmlNodes: [],
			tableList: [{
					week: '第1周',
					content: '我用代码做贺卡：角色'
				},
				{
					week: '第2周',
					content: '夺宝奇兵：游戏中国的运动'
				}, {
					week: '第3周',
					content: '丛林世界：数字动画的原理'
				}, {
					week: '第4周',
					content: '壮志凌云：游戏控制'
				}, {
					week: '第5周',
					content: '给吸尘器编程：条件和判断'
				}
			]
		}
	},
	onLoad(event) {
		console.log(event)
		let html =
			`{"author_name":"未来城不落","cover":"https://pic.36krcnd.com/201910/24080103/jya8idhhpaucga5k!heading","id":101499,"post_id":"5259177","published_at":"37分钟前","title":"深圳年内第二次集中供地 起始总价超130亿元"}`
		// TODO 后面把参数名替换成 payload
		// const payload = event.detailDate || event.payload;
		const payload = html || html;
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.banner = JSON.parse(decodeURIComponent(payload));
		} catch (error) {
			this.banner = JSON.parse(payload);
		}
		// uni.setNavigationBarTitle({
		// 	title: this.banner.title
		// });
		this.getDetail();
	},
	onShareAppMessage() {
		return {
			title: this.banner.title,
			path: DETAIL_PAGE_PATH + '?detailDate=' + JSON.stringify(this.banner)
		}
	},
	onNavigationBarButtonTap(event) {
		const buttonIndex = event.index;
		if (buttonIndex === 0) {
			// 分享 H5 的页面
			const shareProviders = [];
			uni.getProvider({
				service: 'share',
				success: (result) => {
					// 目前仅考虑分享到微信
					if (result.provider && result.provider.length && ~result.provider.indexOf('weixin')) {
						const channels = _handleShareChannels(result.provider);
						uni.showActionSheet({
							itemList: channels.map(channel => {
								return channel.text;
							}),
							success: (result) => {
								const tapIndex = result.tapIndex;
								uni.share({
									provider: 'weixin',
									type: 0,
									title: this.banner.title,
									scene: tapIndex === 0 ? 'WXSceneSession' : 'WXSenceTimeline',
									href: 'https://uniapp.dcloud.io/h5' + DETAIL_PAGE_PATH + '?detailDate=' + JSON.stringify(this.banner),
									imageUrl: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png'
								});
							}
						});
					} else {
						uni.showToast({
							title: '未检测到可用的微信分享服务'
						});
					}
				},
				fail: (error) => {
					uni.showToast({
						title: '获取分享服务失败'
					});
				}
			});
		}
	},
	methods: {
		getDetail() {
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + this.banner.post_id,
				success: (data) => {
					if (data.statusCode == 200) {
						var htmlString = data.data.content.replace(/\\/g, "").replace(/<img/g, "<img style=\"display:none;\"");
						this.htmlNodes = htmlParser(htmlString);
					}
				},
				fail: () => {
					console.log('fail');
				}
			});
		}
	}
}
